import styled from "styled-components";

export const ScreenEffectSty = styled.div`
	-webkit-box-shadow: inset 0px 0px 38px -3px #4f4f4f;
	box-shadow: inset 0px 0px 36px -3px #4f4f4f;
	position: absolute;
	border-radius: inherit;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	pointer-events: none;
`;
